// --- MAIN PAGE ---

export default function HighlightPage() {
    const [highlights, setHighlights] = useState([]);
    const [page, setPage] = useState(1);
    const [loading, setLoading] = useState(false);
    const [hasMore, setHasMore] = useState(true);

    // Filter States
    const [searchQuery, setSearchQuery] = useState('');

    // Observer for Infinite Scroll
    const observer = useRef();
    const lastElementRef = useCallback(node => {
        if (loading) return;
        if (observer.current) observer.current.disconnect();

        observer.current = new IntersectionObserver(entries => {
            if (entries[0].isIntersecting && hasMore) {
                setPage(prevPage => prevPage + 1);
            }
        });

        if (node) observer.current.observe(node);
    }, [loading, hasMore]);

    const {data:highlights, isLoading, isError} = useHighlight();
    if(isLoading) return null;
    if(isError) return <div>Error</div>;
    const highlightArray = Array.isArray(highlights) ? highlights : (highlights?.data || []);

    // Filter Logic (Client-side simulation)
    const filteredHighlights = useMemo(() => {
        return highlights.filter(item => {
            // Removed Category Logic, keeping only Search
            return item.title.toLowerCase().includes(searchQuery.toLowerCase());
        });
    }, [highlights, searchQuery]);

    return (
        <div className="min-h-screen bg-[#F8F9FA] font-sans text-slate-800">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">

                <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">

                    {/* --- LEFT SIDE: CONTENT (8 Columns) --- */}
                    <div className="lg:col-span-8">

                        {/* New Combined Header */}
                        {/*<HeaderWithSearch*/}
                        {/*    title="Highlights"*/}
                        {/*    searchQuery={searchQuery}*/}
                        {/*    onSearchChange={setSearchQuery}*/}
                        {/*/>*/}

                        {/* Grid Layout */}
                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                            {filteredHighlights.map((item, index) => {
                                if (filteredHighlights.length === index + 1) {
                                    return <div ref={lastElementRef} key={item.id} className="h-full"><HighlightCard item={item} /></div>;
                                } else {
                                    return <div key={item.id} className="h-full"><HighlightCard item={item} /></div>;
                                }
                            })}

                            {filteredHighlights.length === 0 && !loading && (
                                <div className="col-span-full py-16 text-center text-gray-500 bg-white rounded-xl shadow-sm border border-gray-100">
                                    <Video size={48} className="mx-auto text-gray-300 mb-4" />
                                    <p>No highlights found matching "{searchQuery}"</p>
                                </div>
                            )}
                        </div>

                        {loading && <Loader />}

                        {!hasMore && filteredHighlights.length > 0 && (
                            <div className="text-center py-8 text-gray-400 text-sm font-medium uppercase tracking-widest">
                                — End of Content —
                            </div>
                        )}
                    </div>

                    {/* --- RIGHT SIDE: SIDEBAR (4 Columns) --- */}
                    <div className="lg:col-span-4 space-y-8">
                        <AdBanner />
                    </div>

                </div>
            </div>
        </div>
    );
}